.jumbotron {
  padding: 1px 10px 10px 15px !important
}

.not-selected {
  color: gray;
}

.hide-transition {
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  opacity: 0;
  visibility: hidden;
}

.show-transition {
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  opacity: 1;
  visibility: visible;
}

img {
  max-height: 200px;
}


