body {
  margin: 0;
  padding: 0;
  font-family: Calibri;
}

.btn {
  font-size: 12px;
  border: none;
  white-space: normal;
  text-align: left;
  box-shadow: none;
  -webkit-box-shadow: none;
}

.url-img {
  max-height: 20px;
  display:inline;
  margin: 5px;
}

.experience-left {
  text-align: right;
}

.experience-right {
  text-align: left;
}

.item-tile {
  padding-top: 10px;
  padding-right: 10px;
  padding-bottom: 20px;
  padding-left: 10px;
}

.institution {
  text-transform: uppercase;
}

.timeperiod {
  font-style: italic;
}

.position, .programme {
  text-transform: capitalize;
}

.profile-img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  max-height: 80px;
  margin-top: 10px;
}

ul {
  list-style: none;
}

.block-title {
  text-transform: uppercase;
  color: rgb(196,89,17)
}

.profile-name {
  text-transform: uppercase;
  color: rgb(196,89,17)
}

.skills .btn {
  background-image: none;
}


